<template>
  <div>
    <el-table
      :data="tableData"
      border>
      <el-table-column
        label="头图"
        width="180">
        <template slot-scope="scope">
          <img :src="scope.row.img" class="img-item">
        </template>
      </el-table-column>
      <el-table-column
        label="标题">
        <template slot-scope="scope">
          <span style="margin-left: 10px">{{ scope.row.title}}</span>
        </template>
      </el-table-column>
      <el-table-column
        label="类型">
        <template slot-scope="scope">
          <span style="margin-left: 10px">{{ scope.row.type}}</span>
        </template>
      </el-table-column>
      <el-table-column
        label="发布时间">
        <template slot-scope="scope">
          <span style="margin-left: 10px">{{ scope.row.createTime}}</span>
        </template>
      </el-table-column>

      <el-table-column label="操作">
      <template slot-scope="scope">
        <el-button
          size="mini"
          @click="edit(scope.row._id)">编辑</el-button>
        <el-button
          size="mini"
          type="danger"
        @click="delData(scope.row._id)">删除</el-button>
        <el-button
          size="mini"
          type="primary"
        @click="create(scope.row._id),dialogTableVisible = true">生成轮播图</el-button>
      </template>
    </el-table-column>
    </el-table>
    <el-dialog title="添加轮播图" :visible.sync="dialogTableVisible" width="80%">
      <el-form ref="form" :model="formData" label-width="100px" size="small">
        <el-form-item label="标题">
          <el-input v-model="formData.title"></el-input>
        </el-form-item>
        <el-form-item label="跳转链接">
          <el-input v-model="formData._id"></el-input>
          <p style="color:#ff6700;font-size: 12px">可填写页面地址，也可填写页面id(新闻列表里)</p>
        </el-form-item>
        <el-form-item label="优先级">
          <el-input-number v-model="formData.priority" size="small" :min="1" :max="10" label="优先级"></el-input-number>
          <p style="color:#ff6700;font-size: 12px">值越大，图片显示越靠前</p>
        </el-form-item>
        <el-form-item label="图片上传">
          <el-upload
            class="avatar-uploader"
            action="https://upload-z1.qiniup.com"
            :show-file-list="false"
            :on-success="upload"
            :data="{token:token}">
            <img v-if="formData.pic" :src="formData.pic" class="avatar">
            <i v-else class="el-icon-plus avatar-uploader-icon"></i>
          </el-upload>
        </el-form-item>
        <el-form-item>
          <el-button type="success" @click="submit">保存</el-button>
        </el-form-item>
      </el-form>
    </el-dialog>
    <pagination @pageSizeChange="pageSizeChange"  @currentPageChange="currentPageChange"></pagination>
  </div>
</template>

<script>
  import pagination from '@/components/pagination'
  export default {
    name: "news",
    components: {
      pagination
    },
    data() {
      return {
        tableData: [],
        dialogTableVisible: false,
        formData: {
          title: '',
          _id: '',
          priority: 1,
          pic: ''
        },
        token: '',
        params: {
          pageSize: 5,
          page: 1
        }
      }
    },
    methods: {
      getdata() {
        this.$axios.post('getNews', this.params).then((res) => {
          this.tableData = res.data
        })
      },
      delData(val) {
        this.$confirm('此操作将永久删除该新闻, 是否继续?', '提示', {
          cancelButtonText: '取消',
          confirmButtonText: '确定',
          type: 'warning'
        }).then(() => {
          this.$axios.post('delNews', {id: val}).then((res) => {
            if (res.code == 200) {
              this.$message({
                message: '新闻删除成功',
                type: 'success'
              });
              setTimeout(() => {
                this.$router.go(0)
              }, 1000)
            }
          })
        }).catch(() => {
          console.log(res.err)
        })

      },
      create(id) {
        this.$axios.post('getNews', {id}).then(res => {
          this.formData = res.data[0]
        })
      },
      submit() {
        console.log(this.formData);
        this.$axios.post('addslideShows', this.formData).then(res => {
          if (res.code == 200) {
            this.$message({
              message: '图片添加成功',
              type: 'success'
            });
          } else {
            this.$message({
              message: res.msg,
              type: 'warning'
            });
          }

        })
      },
      edit(id) {
        this.$router.push({path: '/crmnews/update', query: {id}})
      },
      upload(res) {
        this.formData.pic = "http://image.yaojunrong.com/" + res.key;
        // this.formData.headers += `<img src="${this.formData.img}"/>`
      },
      getToken() {
        this.$axios.qiniuGet().then(res => {
          this.token = res.data
        })
      },
      pageSizeChange(val) {
        this.params.pageSize = val;
        this.getdata()
      },
      currentPageChange(val) {
        this.params.page = val;
        this.getdata()
      },
    },
    created() {
        this.$nextTick(() => {
          this.getdata()
          this.getToken()
        })
      }
  }
</script>
<style>
  .avatar-uploader .el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
  }
  .avatar-uploader .el-upload:hover {
    border-color: #409EFF;
  }

  .avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 178px;
    height: 178px;
    line-height: 178px;
    text-align: center;
  }
  .avatar {
    width: 178px;
    height: 178px;
    display: block;
  }
</style>
<style scoped>
  .img-item{
    width: 100px;
    height: 80px;
  }
</style>



